.house_box {
    padding: 0.3rem 5%;
    border-bottom: 6px solid #eee;
}
.house_detail {
    position: relative;
    h5 {
        font-size: 0.3rem;
        font-weight: bold;
    }
    .house_img {
        position: relative;
        .house_menu {
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            justify-content: space-between;
            div{
                padding: 0.5rem;
                i {
                    font-size: 0.3rem;
                    color: #fff;
                    padding: 0.2rem;
                    border-radius: 50%;
                    background: rgba(0,0,0,.2);
                }
            }
            .heart{
                text-align: right;
            }
        }
    }
    .house_body{
        width:90%;
        margin: 0.2rem auto 0;
        .kicker_content{
            font-size: 0.26rem;
            color: #767676;
            font-weight: bold;
            span {
                margin-right: 0.1rem;
            }
        }
        h2{
            font-size: 0.38rem;
            font-weight: bold;
            color: #484848;
            line-height: 0.5rem;
            margin: 0.2rem 0;
        }
        .house_tags{
            display: flex;
            flex-wrap: wrap;
            li{
                text-align: center;
                font-size: 0.24rem;
                display: inline-block;
                padding: 5px 12px;
                background-color: #f0f0f0;
                border-radius:13px;
                margin: 0 0.2rem 0.2rem 0;
                font-weight: bold;
            }
            .first{
                color: #296E00;
                background-color: #EAF7EA;
            }
            .second{
                color: #8A2400;
                background-color: #f7f3ea;
            }
        }
    }
    .house_path {
        border-top: 1px solid #eee;
        p {
            font-size: 0.28rem;
            margin-top: 0.3rem;
        }
    }
    .house_comment {
        .comment_hd {
            padding: 0.3rem 0;
            display: flex;
            img {
                width: 0.8rem;
                height: 0.8rem;
                border-radius: 50%;
            }
            .comment_user {
                line-height: 0.4rem;
                padding-left: 0.3rem;
                span {
                    font-size: 0.26rem;
                }
            }
        }
        p {
            font-size: 0.28rem;
            line-height: 0.34rem;
            margin-bottom: 0.1rem;
        }
    }
    .house_master {
        .master_info {
            padding-top: 0.3rem;
            display: flex;
            justify-content: space-around;
            align-items: center;
            .master_name {
                img {
                    width: 1rem;
                    height: 1rem;
                    border-radius: 50%;
                }
            }
            .master_phone {
                h5 {    
                    font-size: 0.3rem;
                    color: #008489;
                    line-height: 0.8rem;
                }
            }
        }
    }
    .house_msg {
        p {
            margin-top: 0.4rem;
        }
    }
    .house_last {
        margin-bottom: 1.8rem;
        border: none;
        border-top: 1px solid #eee;
        p {
            margin-top: 0.4rem;
        }
    }
    .house_button {
        width: 100%;
        height: 1.8rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #fff;
        padding: 0.2rem 5%;
        box-sizing: border-box;
        border-top: 1px solid #eee;
        position: fixed;
        bottom: 0;
        z-index: 999;
        span {
            font-size: 0.28rem;
            b {
                font-size: 0.32rem;
                font-weight: bold;
                margin-right: 0.2rem;
            }
        }
        .am-list-item {
            width: 3.2rem;
            height: 1rem;
            color: #fff;
            background: #ff5a5f;
            border: none;
            border-radius: 5px;
            padding: 0;
            .am-list-line {
                padding: 0;
                .am-list-arrow {
                    display: none;
                }
                .am-list-content {
                    color: #fff;
                    text-align: center;
                }
            }
        }
    }
}